  public getSettimana(): Date[] {
    var oggi = new Date(); /** Mi calcolo la data di oggi */
    if (oggi.getDay() == 0) {
      var offeset = 6
    }
    else {
      var offeset = oggi.getDay() - 1;
    }
    var dataIniziale = oggi.getDate() - offeset; /** Mi calcolo la data di inizio settimana sottraendo alla data
                                                      odierna l'offset*/

    /** E incrementandola mi calcolo tutte le altre */
    var luned = new Date(oggi.setDate(dataIniziale));
    var marted = new Date(oggi.setDate(dataIniziale + 1));
    var mercoled = new Date(oggi.setDate(dataIniziale + 2));
    var gioved = new Date(oggi.setDate(dataIniziale + 3));
    var venerd = new Date(oggi.setDate(dataIniziale + 4));

    return [luned, marted, mercoled, gioved, venerd]
  }

